import { useState, useEffect } from 'react'
import useStats from "../src/useStats";
import { makeStyles } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/core/styles';
import theme from '../src/theme';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles(theme => ({
    margin: {
      margin: theme.spacing(1),
    },
    paper: {
        padding: theme.spacing(1),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        minHeight: 235
      },

  }));




export default function Stats({url}) {
    const {stats, loading, error} = useStats(url);
    const classes = useStyles();

    if (loading) return <Paper className={classes.paper}><Typography variant="h6" component="h6" gutterBottom>Loading...</Typography></Paper>;
    if (error) return <Paper className={classes.paper}><Typography variant="h6" component="h6" gutterBottom>No data</Typography></Paper>
    // {error.message.split('not')[0]}
  
    return (
        <React.Fragment>
             <Paper  className={classes.paper} >                        
                <Typography variant="h5" component="h5" gutterBottom>
                    Confirmados
                </Typography>
                <Typography variant="h6" component="h6" gutterBottom>
                    {stats.confirmed.value}
                </Typography>
                <Typography variant="h5" component="h5" gutterBottom>
                    Muertos
                </Typography>
                <Typography variant="h6" component="h6" gutterBottom>
                    {stats.deaths.value}
                </Typography>
                <Typography variant="h5" component="h5" gutterBottom>
                    Curados
                </Typography>
                <Typography variant="h6" component="h6" gutterBottom>
                    {stats.recovered.value}
                </Typography>
            </Paper>
      </React.Fragment>
                
    );
}